import IconClock from '@wikia/react-common/icons/IconClockSmall.esm';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';

import Modal from '../Modal';
import { setStateToInProgress } from '../../stores/QuizStore/QuizStoreActions';

import QuizCountDown from './QuizCountdown';
import Header from './Header';

const TitleScreenHeading = styled.div`
    align-items: center;
    color: ${({ theme }) => theme.color.white};
    display: flex;
    flex: auto;
    font-size: ${({ theme }) => theme.font_size.xxxxl};
    line-height: 1.15;
    padding: 0 36px;
    text-align: center;

    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.xxl};
        padding-left: 12px;
        padding-right: 12px;
    }
`;

const Divider = styled.div`
    background: #fff;
    height: 1px;
    margin: 0 12px;
    opacity: .5;

    @media ${({ theme }) => theme.media.medium_up} {
        height: 2px;
        margin: 0 36px;
        opacity: 1;
    }
`;

const QuizHelp = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};
    font-weight: ${({ theme }) => theme.font_weight.medium};
    margin: 25px 0;
    text-align: center;
`;

const QuizHelpDescription = styled.div`
    font-size: ${({ theme }) => theme.font_size.s};
    font-weight: ${({ theme }) => theme.font_weight.light};
    line-height: 1.5;
    text-align: center;

    @media ${({ theme }) => theme.media.medium_up} {
        font-size: ${({ theme }) => theme.font_size.base};
    }
`;

const Clock = styled(IconClock)`
    padding-right: 6px;
    vertical-align: text-top;
`;

const TitleScreenBody = styled.div`
    padding: 0 12px;

    @media ${({ theme }) => theme.media.medium_up} {
        padding: 0 76px;
    }
`;

function TitleScreen({ state, dispatch }) {
    return (
        <Modal Header={<Header {...state} />} image={state.image}>
            <TitleScreenHeading>{state.title}</TitleScreenHeading>
            <Divider />
            <TitleScreenBody>
                <QuizHelp>
                    <Clock height={18} fill="white" />
                    This quiz is timed!
                </QuizHelp>
                <QuizHelpDescription>
                    Youll have 10 seconds to complete each question. If you dont complete a question, its counted as a wrong answer. Good luck! The quiz starts in.
                </QuizHelpDescription>
                <QuizCountDown onComplete={() => dispatch(setStateToInProgress())} />
            </TitleScreenBody>
        </Modal>
    );
}

TitleScreen.propTypes = {
    dispatch: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const mapDispatchToProps = dispatch => ({
    dispatch,
});

const ConnectedTitleScreen = connect(
    mapStateToProps,
    mapDispatchToProps
)(TitleScreen);


export default ConnectedTitleScreen;
