import { isBrowser, getServicesBaseURL } from '@wikia/react-common/utils/environment';

// eslint-disable-next-line
import pkg from '../package.json';

export function getCommunityName() {
    if (isBrowser() && window.location && window.location.hostname) {
        return window.location.hostname.split('.')[0];
    }

    console.error('Cannot detect community name');
    return '';
}

export function getQuizzesPath(path = '') {
    // return `http://localhost:8080${path}`; // COMMENT IN WHEN TESTING LOCAL todo convert to env
    return `${getServicesBaseURL()}quizzes${path}`;
}

const Config = {
    GA_TRACKING_ID: '',
    INTERNAL_TRACKING_BASE: 'https://beacon.wikia-services.com/__track',
    VERSION: pkg.version,
    NAME: pkg.name,
    TRACKING_APP_NAME: 'quiz_consumption',
};

export default Config;
