import PropTypes from 'prop-types';

// TODO Actually use this file :P

export const AnswerShapeType = PropTypes.shape({
    id: PropTypes.number.isRequired,
    isCorrect: PropTypes.bool.isRequired,
    isSelected: PropTypes.bool,
    reference: PropTypes.string,
    text: PropTypes.string,
});

export const QuestionShapeType = PropTypes.shape({
    answers: PropTypes.arrayOf(AnswerShapeType),
    id: PropTypes.number.isRequired,
    text: PropTypes.string,
    type: PropTypes.string.isRequired,
});

export const QuizShape = PropTypes.shape({
    creatorId: PropTypes.string,
    creatorName: PropTypes.string,
    id: PropTypes.number.isRequired,
    image: PropTypes.string,
    questions: PropTypes.arrayOf(QuestionShapeType),
    siteId: PropTypes.string,
    title: PropTypes.string,
});

export const EventShapeType = PropTypes.shape({
    action: PropTypes.string.isRequired,
    category: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
});
