import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import HideComponent from '@wikia/react-common/components/HideComponent.esm';
import IconCheckmark from '@wikia/react-common/icons/IconCheckmark.esm';
import IconLinkTiny from '@wikia/react-common/icons/IconLinkTiny.esm';
import IconArrowTiny from '@wikia/react-common/icons/IconArrowTiny.esm';

import { connect } from 'react-redux';

import useInterval from '@wikia/react-common/hooks/useInterval.esm';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';
import { trackClick } from '../../utils/tracking/tracking';

import QuestionTitle from './QuestionTitle';

const CorrectAnswerBox = styled.div`
    border-radius: 3px;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.3);
    background-color: ${({ theme }) => theme.color.white};
    margin-bottom: 24px;
    padding: 30px;
    text-align: center;
`;

const Icon = styled(IconCheckmark)`
    fill: ${({ theme }) => theme.color.success};
    height: 34px;
    margin-bottom: 12px;
    width: 34px;
`;

const Text = styled.div`
    font-size: ${({ theme }) => theme.font_size.xl}
    color: ${({ theme }) => theme.color.success};

    @media ${({ theme }) => theme.media.medium_up} {
        font-size: 30px; // Not in DS
    }
`;

const LinkIcon = styled(IconLinkTiny)`
    fill: ${({ theme }) => theme.color.fandom_dark_gray};
    height: 12px;
    margin-right: 6px;
    width: 12px;

    &:hover {
        color: ${({ theme }) => theme.color.fandom_middle_gray};
    }
`;

const LearnMore = styled.a`
    align-items: center;
    color: ${({ theme }) => theme.color.fandom_dark_gray};
    display: flex;
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    justify-content: center;
    margin: 30px 0;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
        color: ${({ theme }) => theme.color.fandom_middle_gray};
    }
`;

const NextIcon = styled(IconArrowTiny)`
    fill: ${({ theme }) => theme.color.fandom_dark_gray};
    width: 12px;
    height: 12px;
    margin-left: 6px;
    transform: scaleX(-1);
`;

const NextQuestion = styled.button`
    background: ${({ theme }) => theme.color.white};
    border-radius: 3px;
    border: 1px solid ${({ theme }) => theme.color.fandom_dark_gray};
    color: ${({ theme }) => theme.color.fandom_dark_gray};
    cursor: pointer;
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    height: auto !important;
    margin: 0 auto;
    padding: 12px 18px;
    text-transform: uppercase;
    width: 154px;
    ${CENTER_WITH_FLEX}

    &:hover {
        background: inherit !important;
    }
`;

function CorrectAnswerScreen({ onNext, state }) {
    const question = state.questions[state.currentQuestionIndex];
    const answer = question.answers.find(a => a.isCorrect);
    const isLastQuestion = state.currentQuestionIndex === (state.questions.length - 1);

    useInterval(onNext, isLastQuestion ? 2000 : null);

    return (
        <React.Fragment>
            <QuestionTitle />
            <CorrectAnswerBox>
                <Icon />
                <Text>{answer.text}</Text>
                <HideComponent hide={!answer.reference}>
                    <LearnMore href={answer.reference} onClick={() => trackClick('learn_more')} target="_blank">
                        <LinkIcon />
                        learn more
                    </LearnMore>
                </HideComponent>
            </CorrectAnswerBox>
            <HideComponent hide={isLastQuestion}>
                <NextQuestion onClick={onNext} data-testid="next question">
                    next question
                    <NextIcon />
                </NextQuestion>
            </HideComponent>
        </React.Fragment>
    );
}


CorrectAnswerScreen.propTypes = {
    onNext: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedCorrectAnswerScreen = connect(
    mapStateToProps,
)(CorrectAnswerScreen);

export default ConnectedCorrectAnswerScreen;
