import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import colorAlpha from 'color-alpha';
import ReactModal from 'styled-react-modal';
import Image from '@wikia/react-common/components/Image.esm';
import CloseIcon from '@wikia/react-common/icons/IconClose.esm';

import ClientStore from '../../context/ClientStore/ClientStore';
import { closeModal } from '../../context/ClientStore/ClientStoreActions';

const StyledModal = ReactModal.styled`
   border-top: 54px solid transparent;

   @media ${({ theme }) => theme.media.medium_up} {
       border: 60px solid transparent;
   }

   @media screen and (max-height: 850px) {
       border-top: 54px solid transparent;
   }
`;

const StyledModalInner = styled.div`
    background: ${({ theme }) => (colorAlpha(theme.contentTypes.color.primaryColor, 0.8))};
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.3);
    height: 100vh;
    max-width: 100vw;
    max-height: calc(100vh - 54px);
    overflow-y: auto;
    position: relative;
    width: 100vw;

    @media screen and (min-height: 716px) {
        border-radius: 4px;
        height: 716px;
    }

    @media screen and (min-width: 700px) {
        border-radius: 4px;
        width: 700px;
    }
`;

const Background = styled(Image)`
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
`;

const ModalContent = styled.div`
    color: ${({ theme }) => theme.color.white};
    display: grid;
    grid-template-rows: 36px 1fr;
    height: 100%;
    padding: 12px;

    @media ${({ theme }) => theme.media.medium_up} {
        padding: 18px 24px 24px;
    }
`;

const ModalHeader = styled.div`
`;

const ModalBody = styled.div`
    display: flex;
    flex-direction: column;
`;

const CancelIconWrapper = styled.div`
    position: absolute;
    right: 11px;
    z-index: 10000000;
    top: 15px;

    @media ${({ theme }) => theme.media.medium_up} {
        position: relative;
        right: calc(-101% - 15px);
        top: -10px;
    }
`;

const CloseIconStyled = styled(CloseIcon)`
    fill: #fff;

    &:hover {
        cursor: pointer;
    }
`;

function Modal({ children, Header, noPadding, image }) {
    const { state, dispatch } = React.useContext(ClientStore);

    const closeModalDispatch = () => dispatch(closeModal());
    const imgUrl = `https://static.wikia.nocookie.net/${image}/smart/width/1060/height/596?format=original`;

    return (
        <StyledModal isOpen={state.isOpen}>
            <CancelIconWrapper>
                <CloseIconStyled height={22} width={22} fill="#fff" onClick={closeModalDispatch} />
            </CancelIconWrapper>
            <StyledModalInner>
                <Background src={imgUrl} alt="background image" />
                <ModalContent noPadding={noPadding}>
                    <ModalHeader>{Header}</ModalHeader>
                    <ModalBody>{children}</ModalBody>
                </ModalContent>
            </StyledModalInner>
        </StyledModal>
    );
}

Modal.propTypes = {
    children: PropTypes.node.isRequired,
    Header: PropTypes.node,
    image: PropTypes.string,
    noPadding: PropTypes.bool,
};

Modal.defaultProps = {
    noPadding: false,
    Header: null,
    image: '',
};

export default Modal;
