import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';

import { connect } from 'react-redux';

import Avatar from '@wikia/react-common/components/StyledAvatar.esm';

import { ONE_LINE_ELLIPSIS } from '../../utils/cssSnippets';

const Wrapper = styled.div`
    align-items: center;
    color: ${({ theme }) => theme.color.white};
    display: flex;
    flex: 1;
    font-size: ${({ theme }) => theme.font_size.base};
    justify-content: space-between;
`;

const StyledAvatar = styled(Avatar)`
    margin-right: 6px;

    @media ${({ theme }) => theme.media.medium_up} {
        margin-right: 12px;
    }
`;

const Title = styled.div`
    flex: 1;
    font-weight: ${({ theme }) => theme.font_weight.medium};
    letter-spacing: 0.3px;
    max-width: calc(100vw - (2 * 12px + 30px + 84px)); // 100vw - (padding + avatar w/margin + Index w/margin below)
    ${ONE_LINE_ELLIPSIS}

    @media ${({ theme }) => theme.media.medium_up} {
        max-width: calc(100vw - (2 * 24px + 36px + 24px + 84px)); // 100vw - (padding + avatar w/margin + Index w/margin below)
    }
`;

const Index = styled.div`
    margin-left: 24px;
    text-align: right;
    width: 60px;
`;


function QuestionHeader({ state }) {
    return (
        <Wrapper>
            <StyledAvatar size={24} user={state.creatorId} />
            <Title>{state.title}</Title>
            <Index>{state.currentQuestionIndex + 1} / {state.questions.length}</Index>
        </Wrapper>
    );
}

QuestionHeader.propTypes = {
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedQuestionHeader = connect(
    mapStateToProps,
)(QuestionHeader);

export default ConnectedQuestionHeader;
