import { connect } from 'react-redux';
import HideComponent from '@wikia/react-common/components/HideComponent.esm';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import IconCrossSmall from '@wikia/react-common/icons/IconCrossSmall';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';

const Answer = styled.div`
    background: ${({ incorrect, theme }) => (incorrect ? theme.color.alert : theme.color.white)} none;
    border-radius: 3px;
    border: 0;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, .3);
    color: ${({ incorrect, theme }) => (incorrect ? theme.color.white : theme.color.black)};
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.base};
    margin-top: 18px;
    max-height: 62px;
    min-height: 44px;
    padding: 12px 38px;
    position: relative;
    text-align: center;
    width: 100%;
    ${CENTER_WITH_FLEX}
`;

const AnswerButton = styled(props => <Answer as="button" {...props} />)`
    cursor: pointer;
    height: auto !important;

    &:hover {
        background: inherit !important;
    }
`;

const CrossIcon = styled(IconCrossSmall)`
    fill: ${({ theme }) => theme.color.white};
    vertical-align: middle;
    position: absolute;
    right: 13px;
    width: 18px;
    display: ${({ incorrect }) => (!incorrect ? 'none' : 'block')};

    @media ${({ theme }) => theme.media.small_down} {
        display: none;
    }
`;

function QuestionAnswers({ onAnswer, answerId, state, timeIsUp }) {
    const question = state.questions[state.currentQuestionIndex];

    // `onAnswer` is present when we want to display list of answers to click
    if (typeof onAnswer === 'function') {
        return (
            <React.Fragment>
                {question.answers.map(answer => (
                    <AnswerButton data-testid={`answer-${answer.id}`} key={answer.id} onClick={() => { if (!timeIsUp) { onAnswer(question.id, answer.id); } }}>
                        {answer.text}
                    </AnswerButton>
                ))}
            </React.Fragment>
        );
    }

    // `answerId` is present when we want to display list of incorrect answers with `answerId` marked red
    if (typeof answerId === 'number') {
        return (
            <React.Fragment>
                {question.answers.map(answer => (
                    <Answer key={answer.id} incorrect={answerId === answer.id}>
                        {answer.text}
                        <HideComponent hide={answerId !== answer.id}>
                            <CrossIcon />
                        </HideComponent>
                    </Answer>
                ))}
            </React.Fragment>
        );
    }

    return null;
}

QuestionAnswers.propTypes = {
    answerId: PropTypes.number,
    onAnswer: PropTypes.func,
    state: PropTypes.object.isRequired,
    timeIsUp: PropTypes.bool.isRequired,
};

QuestionAnswers.defaultProps = {
    onAnswer: undefined,
    answerId: undefined,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedQuestionAnswers = connect(
    mapStateToProps,
)(QuestionAnswers);

export default ConnectedQuestionAnswers;
