import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';

import ClientStore from '../context/ClientStore/ClientStore';
import withProviders from '../hocs/withProviders';
import { QUIZ_DATA_STATES, resetRedux } from '../stores/QuizStore/QuizStoreActions';
import { initializeTracking, trackClick } from '../utils/tracking/tracking';
import { loadQuiz } from '../stores/QuizStore/QuizStoreThunks';
import { openModal, closeModal } from '../context/ClientStore/ClientStoreActions';

import QuizModalWrapper from './QuizModalWrapper';

/**
 * Component exported from the library
 */
function QuizController({ quizId, open, options, reduxState, reduxDispatch, onClose }) {
    const onClick = (e) => {
        // On the feed page, the modal is a child of the post component.
        // This stops propagation to that post component's click handler.
        e.stopPropagation();
    };

    // do this only once
    React.useEffect(() => {
        // if there's an `options` prop that means the quiz is running in the React env (and we need to initialize tracking)
        if (typeof options !== 'undefined') {
            initializeTracking(options);
        }

        trackClick('start');
    }, []);

    const { state, dispatch } = React.useContext(ClientStore);

    // on mount and when the quiz changes, load new data
    React.useEffect(() => {
        if (reduxState.dataState !== QUIZ_DATA_STATES.LOADED || quizId !== reduxState.id) {
            reduxDispatch(loadQuiz(quizId));
        }
    }, [quizId]); // eslint-disable-line react-hooks/exhaustive-deps

    if (reduxState.dataState !== QUIZ_DATA_STATES.LOADED) {
        return null;
    }

    // check if we should start with open or closed modal
    if (open && state.isOpen !== false) {
        dispatch(openModal());
    } else {
        // reset redux
        reduxDispatch(resetRedux());
        dispatch(closeModal());
    }

    if (state.isOpen === false && typeof onClose === 'function') {
        onClose();
    }

    return <QuizModalWrapper onClick={onClick} />;
}

QuizController.propTypes = {
    onClose: PropTypes.func,
    open: PropTypes.bool,
    options: PropTypes.object,
    quizId: PropTypes.number.isRequired,
    reduxDispatch: PropTypes.func.isRequired,
    reduxState: PropTypes.object.isRequired,
};

QuizController.defaultProps = {
    onClose: () => {},
    open: false,
    options: undefined,
};

const mapStateToProps = state => ({
    reduxState: state,
});

const mapDispatchToProps = dispatch => ({
    reduxDispatch: dispatch,
});

const ConnectedQuizController = connect(
    mapStateToProps,
    mapDispatchToProps
)(QuizController);

export default withProviders(ConnectedQuizController);
