function isBrowser() {
  return typeof window !== 'undefined';
}
function isFandomCom() {
  if (isBrowser() && window.location && window.location.hostname) {
    return window.location.hostname.includes('fandom.com');
  } // doesn't work on SSR


  return false;
}
function getServicesBaseURL() {
  return isFandomCom() ? 'https://services.fandom.com/' : 'https://services.fandom-dev.us/';
}
function getEventLoggerBase() {
  return "".concat(getServicesBaseURL(), "event-logger");
}

function logErrorEvent(app, version, name) {
  var data = arguments.length > 3 && arguments[3] !== undefined ? arguments[3] : {};

  // eslint-disable-next-line no-restricted-globals
  if (!self.fetch) {
    console.error('Env does not have fetch');
    return;
  }

  fetch("".concat(getEventLoggerBase(), "/error"), {
    method: 'POST',
    // *GET, POST, PUT, DELETE, etc.
    mode: 'cors',
    // no-cors, cors, *same-origin
    credentials: 'include',
    // include, *same-origin, omit
    headers: {
      'Content-Type': 'application/json'
    },
    redirect: 'follow',
    // manual, *follow, error
    referrer: 'no-referrer',
    // no-referrer, *client
    body: JSON.stringify({
      name: name,
      description: JSON.stringify(data),
      client: app,
      client_version: version
    }) // body data type must match "Content-Type" header

  }).catch(function () {
    console.error('Event logging error');
  });
}

export default logErrorEvent;
