const ICON_SIZE = 22;

const SCORE_EMOJI = {
    UH_OH: ['', '', ''],
    NEUTRAL: ['', '', ''],
    GOOD: ['', '', ''],
    NAILED_IT: ['', '', ''],
};

function isCorrect(question) {
    const correctAnswer = question.answers.find(answer => answer.isCorrect);

    return correctAnswer.isSelected === true;
}

function accuracyToEmoji(accuracy) {
    const rand = Math.floor(Math.random() * 3);

    if (accuracy < 0.25) {
        return SCORE_EMOJI.UH_OH[rand];
    }

    if (accuracy < 0.50) {
        return SCORE_EMOJI.NEUTRAL[rand];
    }

    if (accuracy < 0.75) {
        return SCORE_EMOJI.GOOD[rand];
    }

    return SCORE_EMOJI.NAILED_IT[rand];
}

// todo move to react-common
function copyToClipbaord(str) {
    const el = document.createElement('textarea');
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
}

function ShareCard({ questions, shareUrl }) {
    const [isResultsCard, showResults] = React.useState(false);

    const numberCorrect = questions.reduce((accum, question) => {
        if (isCorrect(question)) {
            return accum + 1;
        }

        return accum;
    }, 0);

    const accuracy = numberCorrect / questions.length;
    const emoji = accuracyToEmoji(accuracy);

    const facebookShareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(shareUrl)}`;
    const twitterShareUrl = `https://twitter.com/share?url=${encodeURIComponent(shareUrl)}`;

    const copyLinkToClipboard = () => copyToClipbaord(shareUrl);
    const toggleResults = () => showResults(!isResultsCard);

    return (
        <ShareCardContainer>
            <HideComponent hide={isResultsCard}>
                <Emoji>{emoji}</Emoji>
            </HideComponent>
            <YouScored>You scored</YouScored>
            <Score data-testid="score">{numberCorrect} / {questions.length}</Score>
            <FooterWrapper>
                <ScorecardLink onClick={toggleResults}>
                    <StyledIconClipboard fill={COLORS.fandom_dark_gray} />
                    Scorecard
                </ScorecardLink>
                <ShareFooter>
                    <SocialMedia>
                        <a
                            onClick={() => trackClick('share_fb')}
                            href={facebookShareUrl}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <IconFacebook height={ICON_SIZE} fill={COLORS.fandom_dark_gray} />
                        </a>
                        <a
                            onClick={() => trackClick('share_twitter')}
                            href={twitterShareUrl}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <StyledIconTwitter height={ICON_SIZE} fill={COLORS.fandom_dark_gray} />
                        </a>
                        <StyledIconLink
                            onClick={() => { trackClick('share_link'); copyLinkToClipboard(); }}
                            height={ICON_SIZE}
                            fill={COLORS.fandom_dark_gray}
                        />
                    </SocialMedia>
                </ShareFooter>
            </FooterWrapper>
            <HideComponent hide={!isResultsCard}>
                <ResultsCard />
            </HideComponent>
        </ShareCardContainer>
    );
}

ShareCard.propTypes = {
    questions: PropTypes.arrayOf(QuestionShapeType).isRequired,
    shareUrl: PropTypes.string.isRequired,
};

export default ShareCard;
