import { connect } from 'react-redux';
import IconArrow from '@wikia/react-common/icons/IconArrowTiny.esm';
import IconComment from '@wikia/react-common/icons/IconComment.esm';
import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';
import { getCommunityName, getQuizzesPath } from '../../Config';
import { trackClick } from '../../utils/tracking/tracking';
import fetchWithLogging from '../../utils/fetchWithLogging';
import Modal from '../Modal';

import ShareCard from './ShareCard';

const Title = styled.h2`
    color: ${({ theme }) => theme.color.white};
    font-size: ${({ theme }) => theme.font_size.l};
    font-weight: ${({ theme }) => theme.font_weight.normal};
    line-height: 1.2;
    margin: 0 0 24px;
    text-align: center;

    @media ${({ theme }) => theme.media.medium_up} {
        font-size: 30px; // Not in DS
    }
`;

const ScoreCardContainer = styled.div`
    flex-direction: column;
    ${CENTER_WITH_FLEX}
`;

const NextQuiz = styled.a`
    align-items: center;
    cursor: pointer;
    color: inherit !important;
    display: flex;
    flex: 1;
    font-size: ${({ theme }) => theme.font_size.xs};
    font-weight: ${({ theme }) => theme.font_weight.bold};
    justify-content: flex-end;
    margin: 36px 0;
    text-transform: uppercase;
`;

const Arrow = styled(IconArrow)`
    margin-left: 4px;
    transform: rotate(180deg);
`;

const Wrapper = styled.div`
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 6px;

    @media ${({ theme }) => theme.media.medium_up} {
        padding: 0 36px;
    }
`;

const Comment = styled(IconComment)`
    fill: ${({ theme }) => theme.color.white};
    height: 24px;
    margin-right: 12px;
    width: 24px;
`;

const JoinConversationLink = styled.a`
    align-items: center;
    color: ${({ theme }) => theme.color.white};
    display: flex;
    font-size: ${({ theme }) => theme.font_size.base};
    line-height: .75;
    margin: 30px 0;
    text-decoration: none;

    @media ${({ theme }) => theme.media.medium_up} {
        margin: 36px 0;
    }
`;

const Divider = styled.div`
    background:${({ theme }) => theme.color.white};
    height: 1px;
    margin: 0 36px;
    width: calc(100% - 12px);

    @media ${({ theme }) => theme.media.medium_up} {
        width: 100%;
    }
`;

function buildHomeUrl(threadId) {
    return `https://${getCommunityName()}.fandom.com/f/p/${threadId}`;
}

function ScoreCard({ state, onNextQuiz }) {
    const homeUrl = buildHomeUrl(state.discussionId);

    React.useEffect(() => {
        const submitAnswers = () => {
            const answers = [];
            Object.values(state.questions).forEach((question) => {
                const selectedAnswer = question.answers.find(answer => answer.isSelected === true);
                answers.push({
                    questionId: question.id,
                    // Possibly there was no answer selected if time ran out
                    answerId: selectedAnswer ? selectedAnswer.id : '',
                });
            });
            fetchWithLogging(getQuizzesPath(`/quizzes/${state.id}/validate_answers`), {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ answers }),
            });
        };

        submitAnswers();
    }, [state.id, state.questions]);

    return (
        <Modal image={state.image}>
            <Wrapper>
                <ScoreCardContainer>
                    <Title>{state.title}</Title>
                    <ShareCard questions={state.questions} shareUrl={homeUrl} />
                </ScoreCardContainer>
                <JoinConversationLink href={homeUrl} onClick={() => trackClick('joined_convo')}>
                    <Comment size={24} />
                    Join the Conversation
                </JoinConversationLink>
                <Divider />
                <NextQuiz onClick={() => { trackClick('next_quiz'); onNextQuiz(); }}>
                    Next Quiz
                    <Arrow height="13" fill="#fff" />
                </NextQuiz>
            </Wrapper>
        </Modal>
    );
}

ScoreCard.propTypes = {
    onNextQuiz: PropTypes.func.isRequired,
    state: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
    state,
});

const ConnectedScoreCard = connect(
    mapStateToProps,
)(ScoreCard);

export default ConnectedScoreCard;
